@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Thai:wght@200;400;500;600&display=swap);
*{
  box-sizing: border-box;
  margin:0;
  font-family: 'IBM Plex Sans Thai', sans-serif;
/*    */
}
body{
  font-size:16px;
  background:#1c1f2e;
}
.coinapp{
  background-color:#1c1f2e;
  color:white;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.coinapp__name{
  color:#d6dfec;
  font-size:2rem;
  margin-bottom:1em;
  text-transform: capitalize;
}
.loader{
  display:flex;
  justify-content: center;
}

@media only screen and (min-width: 700px) {
  .coin__container{
    display:flex;
    flex-wrap: wrap;
    padding:.5em;
    /* padding:1em; */
  }

}

@media only screen and (min-width: 850px) {
  .coin__container{
    display:flex;
    flex-wrap: wrap;
    padding:.5em;
    justify-content: space-around;
    /* padding:1em; */
  }

}
.coin{
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color: /* #1f2337 */#282a34;;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    border-radius: 20px;
    margin: 2em auto;
    padding: 1em;
    width: 90%;
    font-size:1.5rem;
    font-weight: 500;
}
.coin__data{
    background: #373a48/* #262a3b */;
    color: #a0a0ae;
    margin-top:.5em;
    padding: .5em 1em;
    border-radius: 10px;
}
.coin__price{text-transform: uppercase;}

.coin__priceChange{
    background: #0d0f32;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: .5em 1.25em;
    margin-top: 1em;
    border-radius: 20px;
}
.coin__priceChange--green,.coin__priceChange--red{
    font-weight: 700;
    font-size: 1.25rem;
}
.coin__priceChange--green{
    color:#0cdc2c;
}
.coin__priceChange--red{
    color: #d11515;

}
.coin__name{
    font-size:1.75rem;
    font-weight:700;
    color:#be8c5c;
}
.coin__updatetime{
    margin-top:1em;
    text-align: right;
    align-self: flex-end;
}
@media only screen and (min-width: 700px) {
    .coin{
        width:45%;
        padding:.5em;
        margin:1em .5em;
    }
}
@media only screen and (min-width: 900px) {
    .coin{
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-end;
        width:45%;
        padding:1em;
        margin:1em .5em;
        font-size: 1.25rem;
    }
    .coin__img{
        width:30%;
        margin-right:1em;
    }
    .coin__name{
        font-size:2rem;
    }  
    .coin__data{
        width:60%;
    }
    .coin__updatetime{
        justify-self: flex-end;
    }
}
@media only screen and (min-width: 1200px) {
    .coin__data{
        font-size:1.5rem;
    }
    .coin__priceChange{font-size:1.25rem;}
    .coin__priceChange--green,.coin__priceChange--red{
        font-size:1.5rem;
    }
}
@media only screen and (min-width: 1400px) {
    .coin__data{
        font-size:1.75rem;
    }
}
.coinsearch__form{
    display:flex;
    flex-direction: column;
}
.coinsearch__input,.coinsearch__input--searchall{
    background: #C33764;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #1D2671, #C33764); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    outline: none;
    border:none;
    border-radius: 5px;
    margin-top:1em;
    margin-bottom:1em;
    padding:.5em;
    color:white;
    width:250px;
}
.coinsearch__input::-webkit-input-placeholder{
    color:#8894a6;
}
.coinsearch__input:-ms-input-placeholder{
    color:#8894a6;
}
.coinsearch__input::placeholder{
    color:#8894a6;
}
.coinsearch__input--searchall{
    background: #FC354C;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #FC354C, #0ABFBC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    }

.currencyDropdown__select{
    background: #1c1f2e;
    color: white;
    outline: none;
    text-transform: uppercase;
    margin-top: 3em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.pagination__ul{
    display:flex;
    padding:0;
    margin:2em 0;
}
.pagination__page {
    color: #706767;
    list-style: none;
    cursor: pointer;
    font-size: 1rem;
    padding:.25em 0;
    text-align: center;
    width:30px;
    border:2px solid #363557;
}
.pagination__page:hover{
    border-color:#49486e;    
}
.pagination__page--active {
    color: #d3d9f4;
    background: #272d49;
    font-weight: 500;
}
